import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Table, Icon, Select, Switch } from 'antd';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
const Option = Select.Option;

function UserAdmin(props) {

    const [data, setdata] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            const response = await SuperFetch.get('/users/users');
            setdata(response.data);
        };
        fetchData();
    }, []);

    const handleRole = (e, id) => {
        SuperFetch.put('/users/user', {id:id, role:e}).then(response => {
            if(response.status === 200) {
                notification('success', 'The user has been updated. No email is sent at this moment')
            } else {
                notification('error', 'Something wrong')
            }
        })
    }    

    const handleActive = (e, id) => {
        SuperFetch.put('/users/user', {id:id, active:e}).then(response => {
            if(response.status === 200) {
                notification('success', 'The user has been updated. No email is sent at this moment')
            } else {
                notification('error', 'Something wrong')
            }
        })
    }    

    const columns = [
        {
            title: 'Name',
            dataIndex: 'id',
            render: (text, record) => (record.firstName + ' ' + record.lastName),
        },
        {
            title: 'Email',
            dataIndex: 'email',
            render: (text, record) => text,
        },
        {
            title: 'Role',
            dataIndex: 'status',
            render: (text, record) => {
                return (
                    <Select defaultValue={record.role} style={{ width: 120 }} onChange={(value) => handleRole(value, record.id)}>
                        <Option value= {0}>Super User</Option>
                        <Option value= {1}>Regular User</Option>
                    </Select>
                )
            },
        },
        {
            title: 'Active',
            dataIndex: 'active',
            render: (text, record) => (<Switch defaultChecked={record.active} onChange={(value) => handleActive(value, record.id)} />),
        },
      ];

    return (
    <>
        <Table columns={columns} dataSource={data} rowKey={'id'}/>
    </>
    );
}

export default connect(
  state => ({ idToken: state.Auth.idToken }),
  {}
)(UserAdmin);
