import React from 'react';
import { Input, Row, Col } from 'antd';
import Button from '@isomorphic/shared/isomorphic/components/uielements/button';
import Form from '@isomorphic/shared/isomorphic/components/uielements/form';
import SuperFetch from '../../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
const FormItem = Form.Item;
function Settings(props) {
  // Declare a new state variable, which we'll call "count"
  const { getFieldDecorator } = props.form;
  const handleSave = e => {
    e.preventDefault();

    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.modifiedBy = props.user.id;
        SuperFetch.put('users/settings', values).then(response => {
          if (response.status === 200) {
            notification('success', 'Setting Updated');
          } else {
            notification('error', 'something goes wrong');
          }
        });
      }
    });
  };

  return (
    <Row type="flex" justify="center">
      <Col span={12}>
        <Form className="isoSignUpForm" onSubmit={handleSave}>
          <FormItem label={<span>Accountant Email:</span>}>
            {getFieldDecorator('accountantEmail', {
              rules: [
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your User Email!',
                },
              ],
            })(
              <Input
                id="inputUserName"
                name="inputUserName"
                size="large"
                placeholder="Accountant Email"
              />
            )}
          </FormItem>
          <FormItem label={<span>Forward Email:</span>}>
            {getFieldDecorator('forwardEmail', {
              rules: [
                {
                  type: 'email',
                  message: 'The input is not valid E-mail!',
                },
                {
                  required: true,
                  message: 'Please input your Forward Email!',
                },
              ],
            })(
              <Input
                id="inputUserName"
                name="inputUserName"
                size="large"
                placeholder="Accountant Email"
              />
            )}
          </FormItem>
          <FormItem label={<span>Payment Report Password:</span>}>
            {getFieldDecorator('paymentPassword', {
              rules: [
                {
                  required: true,
                  message: 'Please input the payment Password!',
                },
              ],
            })(
              <Input
                id="paymentPassword"
                name="paymentPassword"
                size="large"
                placeholder="Payment Password"
              />
            )}
          </FormItem>
          <FormItem label={<span>ExchangeRate Update Date:</span>}>
            <span>{props.exchangeRateDate}</span>
          </FormItem>
          <Button type="primary" htmlType="submit">
            SAVE
          </Button>
        </Form>
      </Col>
    </Row>
  );
}

const WrappedSettings = Form.create({
  mapPropsToFields(props) {
    return {
      accountantEmail: Form.createFormField({
        ...props.accountantEmail,
        value: props.settings.accountantEmail,
      }),
      forwardEmail: Form.createFormField({
        ...props.forwardEmail,
        value: props.settings.forwardEmail,
      }),
      paymentPassword: Form.createFormField({
        ...props.paymentPassword,
        value: props.settings.paymentPassword,
      }),
    };
  },
})(Settings);

export default WrappedSettings;
